

import ModalBase from "@/components/modal/ModalBase";
import VueNextSelect from "vue-next-select";
import "vue-next-select/dist/index.min.css";

export default {
  name: "ModalRegister",
  components: {
    ModalBase,
    VueNextSelect,
  },
  props: {
    openModalLogin: Function,
  },
  data() {
    return {
      username: "",
      usernameError: "",
      email: "",
      emailError: "",
      password: "",
      passwordError: "",
      group: null,
      groupError: "",
      groups: [
        { title: "ОИБ-418", id: 1 },
        { title: "ОИБ-318", id: 2 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
      ],
    };
  },
  methods: {
    open() {
      return this.$refs.modalBase.open();
    },
    close() {
      return this.$refs.modalBase.close();
    },
    regexValidation(re, str) {
      return re.test(str);
    },
    usernameValidation() {
      if (!this.regexValidation(/^[\w \-А-я_]{1,128}$/, this.username)) {
        this.usernameError = "Некорректное имя пользователя";
        return false;
      }
      this.usernameError = "";
      return true;
    },
    passwordValidation() {
      if (this.password.length < 6 || this.password.length >= 20) {
        this.passwordError = "Длина пароля 6-20 символов";
        return false;
      }
      this.passwordError = "";
      return true;
    },
    groupValidation() {
      if (!this.group || !Object.entries(this.group).length) {
        this.groupError = "Группа обязательное поле";
        return false;
      }
      this.groupError = "";
      return true;
    },
    emailValidation() {
      if (!this.regexValidation(/^([\wА-я.-]+)@([\w.-]+)\.([A-z]{1,10})$/, this.email)) {
        this.emailError = "Некорректный E-mail";
        return false;
      }
      this.emailError = "";
      return true;
    },
    confirm() {
      if (!this.isValidForm) return;
      return this.$refs.modalBase.confirm({
        username: this.username,
        email: this.email,
        group: this.group?.id,
        password: this.password,
      });
    },
    async showError() {
      return new Promise(resolve => {
        this.$refs.modalBase.showError();
        setTimeout(() => {
          this.$refs.modalBase.hideError();
          resolve();
        }, 1000);
      });
    },
    async showSuccess() {
      return new Promise(resolve => {
        this.$refs.modalBase.showSuccess();
        setTimeout(() => {
          this.fio = "";
          this.phone = "";
          this.comment = "";
          this.$refs.modalWindowMask.clearPhone();
          this.$refs.modalBase.hideSuccess();
          this.$refs.modalBase.close();
          resolve();
        }, 1000);
      });
    },
  },
  computed: {
    isValidForm() {
      this.usernameValidation();
      this.passwordValidation();
      this.emailValidation();
      this.groupValidation();
      return !(this.usernameError.length || this.passwordError.length || this.emailError.length || this.groupError.length);
    },
  },

};
