

import ModalBase from "@/components/modal/ModalBase";

export default {
  name: "ModalLogin",
  components: {
    ModalBase,
  },
  props: {
    openModalRegister: Function,
  },
  data() {
    return {
      username: "",
      usernameError: "",
      password: "",
      passwordError: "",
    };
  },
  methods: {
    open() {
      return this.$refs.modalBase.open();
    },
    close() {
      return this.$refs.modalBase.close();
    },
    regexValidation(re, str) {
      return re.test(str);
    },
    usernameValidation() {
      console.log(this.username);
      if (!this.regexValidation(/^[\w \-А-я _]{1,128}$/, this.username)) {
        this.usernameError = "Некорректное имя пользователя";
        return false;
      }
      this.usernameError = "";
      return true;
    },
    passwordValidation() {
      if (this.password.length < 6 || this.password.length >= 20) {
        this.passwordError = "Длина пароля 6-20 символов";
        return false;
      }
      this.passwordError = "";
      return true;
    },
    confirm() {
      if (!this.isValidForm) return;
      return this.$refs.modalBase.confirm({
        username: this.username,
        password: this.password,
      });
    },
    async showError() {
      return new Promise(resolve => {
        this.$refs.modalBase.showError();
        setTimeout(() => {
          this.$refs.modalBase.hideError();
          resolve();
        }, 1000);
      });
    },
    async showSuccess() {
      return new Promise(resolve => {
        this.$refs.modalBase.showSuccess();
        setTimeout(() => {
          this.fio = "";
          this.phone = "";
          this.comment = "";
          this.$refs.modalWindowMask.clearPhone();
          this.$refs.modalBase.hideSuccess();
          this.$refs.modalBase.close();
          resolve();
        }, 1000);
      });
    },
  },
  computed: {
    isValidForm() {
      this.usernameValidation();
      this.passwordValidation();
      return !(this.usernameError.length || this.passwordError.length);
    },
  },
};
