
const filesize = require("filesize");

import SlideUpDown from "vue3-slide-up-down";

// CodeMirror
import CodeMirror from "codemirror/lib/codemirror.js";
import "codemirror/addon/scroll/simplescrollbars";
import "codemirror/lib/codemirror.css";
import "codemirror/mode/python/python";
import "@/assets/css/codemirror-theme.css";
import "codemirror/addon/scroll/simplescrollbars.css";

import "codemirror/addon/hint/show-hint.css";
import "codemirror/addon/hint/show-hint.js";
import "codemirror/addon/hint/anyword-hint";
import {Axios} from "@/assets/js/http-common";

export default {
  name: "TaskDetail",
  props: {
    getSolvedSuffix: Function,
    getUsersSuffix: Function,
  },
  components: {
    SlideUpDown,
  },
  data() {
    return {
      isOpenHints: false,
      isOpenStatistics: false,
      durationTime: 300,
      code: "",
      codeMirror: null,
      task: {},
    };
  },
  created() {
    this.getTaskDetail();
  },
  mounted() {
    if (this.$refs.codeTextArea) {
      this.codeMirror = CodeMirror.fromTextArea(this.$refs.codeTextArea, {
        mode: "python",
        lineNumbers: true,
        theme: "code-skill",
        indentUnit: 4,
        scrollbarStyle: "overlay",
      });
    }
  },
  methods: {
    async getTaskDetail() {
      try {
        const { data } = await Axios.get(
          `/categories/${this.$route.params.categorySlug}/tasks/${this.$route.params.taskId}/`,
        );
        this.task = data?.task || {};
      } catch (error) {
        console.error(error);
      }
    },
    getFileSize(size) {
      return filesize(size);
    },
  },
};
